@import 'vars';
@import 'mixins';
@import 'popups/add-adress-popup';


.btn-center-block {
    margin: 0;
    .dashed-btn {
        height: 30px;
        line-height: 30px;
    }
}
.account {
    background-color: #fff;
    display: flex;
    &__sidebar {
        width: 310px;
        padding: 20px 10px;
        border-right: 1px solid $gray-border;
        min-height: 100%;
    }
    &__main {
        width: 890px;
    }
}
.tabs {
    max-width: none;
    &__list {
        padding: 0;
        margin: 0;
    }
    &__item {
        ul {
            margin: 0;
        }
    }
    &__controls-link {
        .feedback__figure {
            vertical-align: initial;
            margin-right: 10px;
        }
        .feedback__icon {
            margin-right: 5px;
            height: 14px;
        }
    }
}

// ---------- sidebar ----------
.sidebar {
    &__email {
        display: block;
        font-size: 12px;
        color: map_get($gray-light, default);
        line-height: 20px;
    }
    &-amount {
        padding-bottom: 15px;
        margin-bottom: 20px;
        border-bottom: 1px solid #f5f5f5;
        margin-top: 1px;
    }
    &-amount__row {
        @include clearfix;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-amount__row-left {
        float: left;
    }
    &-amount__row-right {
        float: right;
        position: relative;
        padding-right: 15px;
        .add-money {
            position: absolute;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            font-weight: 700;
            font-size: 14px;
            color: map_get($green, default);
            vertical-align: middle;
        }
    }
    &__amount-text {
        font-size: 14px;
        color: map_get($gray-light, default);
    }
    &__amount-value {
        font-weight: 700;
        font-size: 14px;
        color: map_get($gray, default);
        display: inline-block;
        vertical-align: middle;
    }
}

.feedback {
    padding-bottom: 15px;
    margin-bottom: 20px;
    border-bottom: 1px solid $gray-border;
    &__item {
        display: block;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__link {
        display: inline-block;
        font-size: 14px;
        font-weight: 700;
        color: map_get($gray, default);
        transition: .3s;
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
    &__icon {
        display: inline-block;
        vertical-align: middle;
        margin-right: 2px;
        width: 16px;
        height: 12px;
        background-size: 0;
        svg {
            display: block;
            width: 100%;
            .st0 {
                fill: map_get($blue, default);
            }
        }
        &--heart {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--envelope {
            background-image: url(data:image/svg+xml;base64,);
        }
        &--dialog {
            background-image: url(data:image/svg+xml;base64,);
        }
    }
    &__figure {
        vertical-align: middle;
        font-size: 16px;
        color: map_get($blue, default);
        font-weight: 700;
    }
}

.sidebar-id {
    padding-bottom: 15px;
    margin-bottom: 20px;
    border-bottom: 1px solid #f5f5f5;
    &-container {
        @include clearfix;
        margin-bottom: 10px;
    }
    &__ava {
        width: 80px;
        height: 80px;
        display: inline-block;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        float: left;
        margin-right: 20px;
        border-radius: 15px;
    }
    &__right {
        float: left;
        // margin-bottom: 12px;
        max-width: 180px;
    }
    &__border {
        background-image: -moz-linear-gradient( 0deg, rgb(74,74,74) 0%, rgb(151,151,151) 100%);
        background-image: -webkit-linear-gradient( 0deg, rgb(74,74,74) 0%, rgb(151,151,151) 100%);
        background-image: -ms-linear-gradient( 0deg, rgb(74,74,74) 0%, rgb(151,151,151) 100%);
        height: 7px;
        width: 100%;
        border-radius: 5px;
    }
    &__greet {
        font-size: 14px;
        color: map_get($gray-light, default);
        margin-bottom: 15px;
        span {
            color: map_get($gray, default);
        }
    }
    .status {
        @include clearfix;
        font-size: 12px;
        color: map_get($gray-light, default);
        margin-bottom: 12px;
        &__left {
            float: left;
            margin-right: 20px;
        }
        &__right {
            float: right;
            max-width: 100px;
            font-style: italic;
        }
        &__value {
            line-height: 14px;
            color: map_get($gray, default);
        }
    }
}

.total-discount {
    font-size: 12px;
    color: map_get($gray-light, default);
    span {
        color: map_get($gray, default);
        padding-left: 5px;
    }
}

.referal {
    padding-bottom: 15px;
    margin-bottom: 20px;
    border-bottom: 1px solid #f5f5f5;
    &__title {
        font-size: 12px;
        color: map_get($gray-light, default);
        margin-bottom: 10px;
    }
    &__copy {
        position: relative;
        margin-bottom: 15px;
        &-input {
            width: 100%;
            font-size: 14px;
            color: #434343;
            border-radius: 3px;
            background-color: #f7f7f7;
            padding: 5px 40px 5px 10px;
            border: 1px solid #ececec;
            outline: none;
        }
        &-btn {
            font-weight: 700;
            color: #fff;
            // height: 28px;
            padding: 5px 15px;
            // text-transform: uppercase;
            border-radius: 3px;
            background-color: map_get($gray-light, default);
            position: absolute;
            right: 0;
            top: 0;
            transition: .3s;
            &:hover {
                background-color: map_get($gray-light, hover);
            }
        }
    }
    &__share {
        text-align: right;
        padding-right: 5px;
    }
}

.recently-viewed {
    max-width: 290px;
    &__title {
        font-size: 14px;
        font-weight: 400;
        color: map_get($gray-light, default);
        margin-bottom: 10px;
    }
    &__item {
        display: block;
        margin-bottom: 10px;
        @include clearfix;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &__item-left {
        float: left;
    }
    &__item-right {
        float: left;
        max-width: 215px;
    }
    &__link {
        display: block;
    }
    &__item-pic {
        width: 60px;
        img {
            display: block;
            max-width: 100%;
            object-fit: cover;
        }
    }
    &__item-title {
        margin-top: 10px;
        margin-left: 10px;
        font-family: $merri-font;
        color: map_get($gray, default);
        font-size: 12px;
        line-height: 14px;
        font-weight: 700;
    }
}

// ---------- MAIN content ----------
.content-filter {
    margin: 10px 0;
    ul {
        li {
            list-style: none;
        }
    }
    .sort-form__select {
        width: 200px;
    }
    &__list {

    }
    &__item {
        display: inline-block;
        margin-right: 15px;
        &:first-child {
            .content-filter__link {
                padding-left: 25px;
            }
        }
        &:last-child {
            margin-right: 0;
        }
        &.active {
            .content-filter__link {
                color: map_get($gray-light, default);
            }
        }
    }
    &__link {
        display: inline-block;
        font-size: 12px;
        font-weight: 700;
        color: map_get($blue, default);
        padding: 10px 15px 10px 15px;
        transition: .3s;
        &:hover {
            color: map_get($blue, hover);
        }
        &:hover {
            color: map_get($blue, active);
        }
    }
}

// ---------- Orders ----------
.order {
    &__id {
        font-size: 14px;
        color: map_get($gray-light, default);
        padding: 10px 25px;
        background-color: #f5f5f5;
        span {
            color: map_get($gray, default);
        }
    }
    &-table {
        width: 100%;
        padding: 15px 15px 0 25px;
        margin-bottom: 15px;
        border-bottom: 1px solid #f5f5f5;
        // border-collapse: collapse;
        // border-spacing: 0;
    }
    &-table__td {
        vertical-align: top;
        &--pic {
            width: 90px;
        }
    }
    &__pic {
        display: block;
        width: 70px;
        img {
            max-width: 100%;
            display: block;
            object-fit: cover;
        }
    }
    &-table-inner {
        // border-spacing: 0;
        // border-collapse: collapse;
        font-size: 0;
        border-spacing: 0;
        td {
            font-size: 12px;
            padding: 0px 10px 10px 0;
            vertical-align: top;
            &:last-child {
                // padding-bottom: 0;
            }
        }
        &__td-label {
            color: map_get($gray-light, default);
            &.label1 {
                width: 86px;
            }
            &.label2 {
                width: 128px;
            }
        }
        &__td-col1 {
            width: 150px;
        }
        &__td-col2 {
            width: 140px;
        }
        &__td-value {
            &.value1 {
                width: 118px;
            }
            &.value2 {

            }
        }
    }
    .order-btn {
        color: map_get($gray, default);
        display: inline-block;
        font-size: 12px;
        font-weight: 700;
        transition: .3s;
        .wtite-seller {
            font-size: 15px;
            font-weight: 700;
            color: map_get($blue, default);
        }
        &:hover {
            color: map_get($gray, hover);
        }
        &:active {
            color: map_get($gray, active);
        }
    }
}

// ---------- Adresses ----------
.addresses {
    &__title {
        font-size: 14px;
        font-weight: 700;
        color: map_get($gray, default);
        font-family: $merri-font;
        padding: 20px 25px;
    }
    &-table {
        width: 100%;
        // padding: 0 15px 0 25px;
        margin-bottom: 15px;    
        border-top: 1px solid $gray-border;
        // border-bottom: 1px solid $gray-border;
        border-spacing: 0;
        tr {
            &:nth-child(odd) {
                background-color: #f7f7f7;
            }
            &:last-child {
                td {
                    border-top: 1px solid #ececec;
                }
            }
        }
        td {
            padding-top: 6px;
            padding-bottom: 6px;
        }
        &__td-label {
            width: 200px;
            font-size: 14px;
            font-weight: 700;
            color: map_get($gray, default);
            padding-left: 25px;
        }
        &__td-text {
            width: 490px;
            padding-right: 20px;
            font-size: 14px;
            line-height: 16px;
            font-weight: 400;
            color: map_get($gray-light, default);
        }
        &__btn {
            font-size: 12px;
            font-weight: 700;
            color: map_get($gray-light, default);
            transition: .3s;
            &:hover {
                color: map_get($gray-light, hover);
            }
            &:active {
                color: map_get($gray-light, active);
            }
        }
        .add-adress__link {
            color: map_get($green, default);
            font-size: 14px;
            font-weight: 700;
            transition: .3s;
            &:hover {
                color: map_get($green, hover);
            }
            &:hover {
                color: map_get($green, active);
            }
        }
        .vcf {
            font-size: 14px;
            font-weight: 700;
            color: map_get($gray, default);
            transition: .3s;
            &:hover {
                color: map_get($gray, hover);
            }
            &:active {
                color: map_get($gray, active);
            }
            span {
                color: map_get($gray-light, default);
                transition: .3s;
            }
        }
    }
}

// ---------- NEWS ----------
.news {
    padding-bottom: 50px;
    &__item {
        display: block;
        border-bottom: 1px solid $gray-border;
        margin-bottom: 15px;
    }
    &__header {
        padding: 9px 40px 9px 25px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background-color: #fafafa;
    }
    &__title {
        max-width: 600px;
        font-family: $merri-font;
        font-size: 16px;
        font-weight: 900;
        color: map_get($gray, default);
    }
    &__date {
        font-size: 12px;
        font-style: italic;
        color: map_get($gray-light, default);
    }
    &__content {
        padding: 10px 40px 15px 25px;
        @include clearfix;
        &-left {
            float: left;
            max-width: 580px;
        }
        &-right {
            float: right;
        }
    }
    &__text {
        color: map_get($gray, default);
        font-size: 14px;
        line-height: 16px;
        font-weight: 400;
        text-indent: 30px;
        padding-top: 10px;
        margin-bottom: 10px;
    }
    .see-all-link {
        font-size: 12px;
        // font-weight: 700;
    }
    &__pic {
        width: 190px;
        height: 100px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
}

// ---------- Competiton ----------
.curr-competition {
    &__title {
        color: #2b2b2b;
        font-family: $merri-font;
        font-size: 14px;
        font-weight: 700;
        padding: 20px 25px;
        &--bg {
            font-weight: 700;
            background-color: #f7f7f7;
            padding: 10px 25px;
        }
    }
    &__bunner {
        max-width: 890px;
        img {
            display: block;
            max-width: 100%;
            height: auto;
        }
    }
    &__text {
        padding: 20px 25px 30px 25px;
        font-size: 14px;
        font-weight: 400;
        color: map_get($gray, default);
    }
    .blue-btn {
        margin-left: 25px;
    }
}