.add-adress-popup {
    width: 710px;
    background-color: #fff;
    padding: 50px 80px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.13);
    &__form {
        &-bottom {
            margin-top: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }   
    &__title {
        color: #2b2b2b;
        font-family: $merri-font;
        font-size: 24px;
        font-weight: 700;
        margin-bottom: 40px;
    }
    &__row {
        margin-bottom: 25px;
        &:nth-child(1) {
            margin-bottom: 35px;
        }
    }
    &__row-adress {
        margin-top: 45px;
        margin-bottom: 50px;
    }
    &__row--2x {
        display: flex;
        justify-content: space-between;
        .form-field-wrap {
            width: 250px;
        }
    }
    .form-field-wrap {
        position: relative;
        &.required:after {
            content: "*";
            display: inline-block;
            color: red;
            position: absolute;
            right: 0;
            top: 0; 
        }
        .form-field-placeholder {
            span {
                text-transform: none;
            }
        }
    }
    &__input {
        outline: none;
        border: none;
        width: 100%;
        border-bottom: 1px solid map_get($gray-light, hover);
        font-size: 18px;
        font-weight: 400;
        color: map_get($gray, default);
        padding-bottom: 6px;
        padding-right: 10px;
        &.non-empty-input {
            border-bottom: 1px solid map_get($gray-light, active);
            & + .form-field-placeholder {
                transform: scale(.90) translateY(-25px);
                span {
                    font-size: 12px;
                }
            }
        }
    }
    button.blue-btn {
        width: 250px;
        height: 42px;
        line-height: 42px;
    }
    .required-fields {
        color: map_get($gray-light, default);
        font-size: 14px;
        font-weight: 400;
        span {
            color: red;
            font-size: 18px;
            vertical-align: middle;
        }
    }
    .vcf {
        display: inline-block;
        text-align: center;
        font-size: 14px;
        font-weight: 700;
        color: map_get($gray-light, default);
        transition: .3s;
        &:hover {
            color: map_get($gray-light, hover);
        }
        &:hover {
            color: map_get($gray-light, active);
        }
    }
}